.model-stat-cards{
    color:rgb(8, 8, 8);
    text-align: center;
    height:auto;
    h2,p{
        margin:10px auto;
        
    } 
    .modelstat-unit{
        font-size:20px;
    }
}
.pop-up-disclaimer{
    position: fixed;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    background-color: rgb(59, 57, 57);
    z-index:20;
    height:auto;
    width:auto;
    border-radius: 10px;
    z-index: 100;
    color: white;
    h2{
        position: relative;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        font-weight: 600;   
        margin: 20px;
        span svg{
            position: absolute;
            right:10px;
            top:2px;
            cursor: pointer;
            background-color: white;
            border-radius: 50%;
            height:20px;
            width:20px;
            padding:5px;
        }
        span svg:hover{
            background-color: rgb(223, 43, 43);
        }
    }
    p{
        border-top: 1px solid rgb(255, 255, 255);
        padding:16px;
    }
}

