  .overview-sliding-gallery {
    scroll-behavior: smooth;
    display: flex;
    overflow-x: scroll;
    align-items: center;

    .overview-sliding-items video,
    .overview-sliding-items img {
      height: 500px;
      width: 1050px;
      margin: 10px;
      object-fit: fill;
     }
  }

  //tablet screens
  @media (min-width: 601px) and (max-width:1200px) {
    .overview-sliding-gallery {

      .overview-sliding-items video,
      .overview-sliding-items img {
        height: 400px;
        width: 600px;
        margin: 10px;
        object-fit: fill;
      }
    }
  }

  //large screen mobiles
  @media (min-width: 350px) and (max-width:600px) {
    .overview-sliding-gallery {

      .overview-sliding-items video,
      .overview-sliding-items img {
        height: 200px;
        width: 300px;
        margin: 10px;
        object-fit: fill;
      }
    }
  }

  //small screen mobiles
  @media (min-width: 1px) and (max-width:349px) {
    .overview-sliding-gallery {
      .overview-sliding-items video,
      .overview-sliding-items img {
        height: 150px;
        width: 250px;
        margin: 10px;
        object-fit: fill;
      }
    }
  }