.overview-sliding-container{
    display: flex;
    overflow-x: scroll;
    margin:70px auto;

    .overview-sliding-items {
        .slide-video,.slide-image{
            width:1150px;
            height:500px;
            padding:10px;
            object-fit: fill;
        }
    }
}
//tablets: -
@media (min-width:601px) and (max-width:1200px){
    .overview-sliding-container{ 
        .overview-sliding-items {
            .slide-video,.slide-image{
                width:auto;
                height:400px;
                padding:8px;
                object-fit: fill;
            }
        }
    }
    
}

//mobile view: -
@media (min-width:301px) and (max-width:600px){
    .overview-sliding-container{ 
        .overview-sliding-items {
            .slide-video,.slide-image{
                width:300px;
                height:200px;
                padding:5px;
                object-fit: fill;
            }
        }
    }
    
}