.Shopping-tools-container{
    display:flex;
    justify-content: space-evenly;
    align-items: center;
    width:1100px;
    flex-wrap: wrap;
    margin:20px auto;
    .shopping-tools-content{
        height:120px;
        display: flex;
        width:240px;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: start;
        flex:1 1 auto;
        padding:10px;
        background-color: #fafafa;
        flex-wrap: wrap;
        color: black;
        .ctadesc{
            font-size: 20px;
            font-weight: 600;
            
        }
        .description{
            margin:10px 0px;
            color: #706f6f;
        }
    }
    .shopping-tools-content:hover{
        background-color:#dedbdb;
    }
}
// tablet: -
@media (min-width:601px) and (max-width:1200px){
    .Shopping-tools-container{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    justify-items:center;  
    align-content: space-evenly;
    width:auto;
    }
    .shopping-tools-content{
        margin:20px auto;
    }
}
//mobile views
@media (min-width:301px) and (max-width:600px){
    .Shopping-tools-container{
    display: grid;
    grid-template-columns: repeat(1,1fr);
    justify-items:center;  
    align-content: space-evenly;
    width:auto;
    }
    .shopping-tools-content{
        margin:20px auto;
    }
}