.mct-container {
    display: flex;
    width: 1100px;
    flex-direction: row-reverse;
    margin:20px auto;
    justify-content: center;
    align-items: center;
    background-color: #fafafa;
    img {
        width: 600px;
        height: 400px;
        padding: 40px;
    }

    .mct-content {
        display: flex;
        width: 35%;
        flex-direction: column;
        justify-content: center;
        align-items: start;
        margin: 0px 20px;
        height: 400px;

        h1 {
            margin: 10px 0px;
            font-weight: 600;
            font-size: 30px;
        }

        p {
            margin: 10px 0px 20px 0px;
            font-size: 16px;
        }

    }
}

//tablet
@media (min-width:601px) and (max-width:1200px) {
    .mct-container {
        flex-direction: column;
        width: 100%;
        justify-content: center;
        margin: 20px auto;
        img{
            max-width:auto;
            max-height: auto;
            padding: 10px;
        }
        .mct-content {
            width:80%;
            display: flex;
            text-align: center;
            align-items: center;
        }
    }
}
//mobile
@media (min-width:300px) and (max-width:600px){
    .mct-container {
        flex-direction: column;
        width: 100%;
        justify-content: center;
        margin: 0px auto;
        img{
            width:100%;
            height: 200px;
            padding: 10px;
            
        }
        .mct-content {
            width: 90%;
            display: flex;
            align-items: center;
            
        }
    }


}