.google-callout-title{
    width:1200px;
    margin:20px 12px 40px auto;
    font-weight: 600;
    font-size: 30px;
}
.google-disclaimer{
    width:80%;
    text-align: center;
    margin:20px auto 80px auto;
    color:rgb(97, 98, 99);
}
.Google-Callouts-container{
    max-width: 1200px;
    width:100%;
    display: flex;
    justify-content: space-evenly;
    align-items: start;
    margin:10px auto 80px auto;
    
    .Google-items{
        width:380px;
        flex-wrap: wrap;
        img{
            width:380px;
            height:auto;
        }
        .google-title{
            margin:10px auto;
            font-size: 20px;
            font-weight: 600;
        }
        .google-desc{
            color:rgb(94, 96, 97);
        }
    }
}
//tablets
@media (min-width:600px) and (max-width:1200px){
    .google-callout-title{
    text-align: center;
    width:100%;
    }
    .Google-Callouts-container{
        width:100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .Google-items{
            width:500px;
            margin:20px auto;
            img{
                width:500px;
            }
        }
    }    
}
//mobile: -
@media (min-width:300px) and (max-width:600px){
    .google-callout-title{
    text-align: center;
    width:90%;
    }
    .Google-Callouts-container{
        width:100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .Google-items{
            width:250px;
            margin:20px auto;
            img{
                width:250px;
            }
        }
    }    
}
