.overview-hero-video {
    position: relative;
    background-color: black;
    video {
        width: 100%;
        height: 100%;
    }
    .overview-hero-body {
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        top: 30%;
        left:50%;
        transform: translate(-50%, -50%);
        color: white; 
        h6{
            padding:15px;
        }
    }
}
.hero-subtitle{
    padding:10px 0px;
}
@media  (min-width: 601px) and (max-width:1200px){
    .overview-hero-body{
        font-size: 14px;
        padding-top:100px;
        text-align: center;
        width:100%;
    }
}
@media (min-width:300px) and (max-width:600px){
    .overview-hero-body{
        position: absolute;
        font-size: 8px;
        padding-top: 120px;
        text-align: center; 
        width: 100%; 
        top:20%; 
        left:50%;
        transform: translate(-50%, -50%);  
    }
    .hero-subtitle{
        padding:8px 0px;
    }
    h6{
        padding:10px;
    }
}