svg {
    height: 20px;
    width: 50px;
}

.featureoverview-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 400px;
    width: 100%;
    padding: 50px;
    background-color: rgb(241, 239, 237);

    .featureoverview-left {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        height: 100%;
        width: 50%;
        align-items: start;

        .feature-description {
            font-size: 37px;
            font-weight: 600;
            color: rgb(125, 128, 131);
            ;

            span {
                color: black;
            }
        }

        .features-link {
            text-align: start;
            font-weight: 600;
            color: #2a609d;
        }

        .features-link::after {
            content: " >"
        }

        .features-link:hover {
            text-decoration: underline;
        }

    }

    .featureoverview-right {
        display: flex;
        justify-content: space-evenly;
        align-items: start;
        width: 50%;
        flex-wrap: wrap;
        margin: auto 50px;
        padding: 100px auto;

        .feature-right-items {
            width: 17%;
            margin: 30px;
        }
    }
}

//tablet view
@media (min-width:300px) and (max-width:1200px) {
    .featureoverview-container {
        flex-direction: column;
        height: auto;
        width: auto;
        justify-content: space-around;
        align-items: start;

        .featureoverview-left
         {
            width: 100%;
            margin: 10px auto;
        }
        .featureoverview-right {
            width:100%;
            margin:0px;
            align-items:start;
            justify-content: flex-start;
        }


        .features-link {
            margin: 40px auto;
        }

    }
}