.discover-container {
    max-width: 1000px;
    width: auto;
    display: flex;
    margin: 40px auto;
    height: auto;
    flex-wrap: wrap;

    .discover-cards {
        width: 480px;
        background-color: rgb(7, 7, 7);
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        margin: 20px auto;
        color: white;
        border-radius: 10px;
        transition: all 2s ease-in-out;

        img {
            max-width: 100%;
            max-height: 100%;
            border-radius: 10px;
        }

        .discover-cards-title {
            font-size: 20px;
            font-weight: 600;
            margin: 10px auto;

        }

        .discover-cards-desc {
            padding: 20px;
        }
    }

    .discover-cards:hover {
        cursor: pointer;
        background-color: rgb(152, 150, 150);
        color: black;
        scale: 105%;
        
    }
}
.blur{
    background: rgba(255, 255, 255, 0.2);
    backdrop-filter: blur(10px);
}

.discover-overlay {
    position: fixed;
    top: 50%;
    right: -201px;
    transform: translate(-50%, -50%);
    background-color: black;
    color: rgb(234, 225, 225);
    border: 1px solid black;    
    width: 400px;
    height: 100vh;   
    z-index: 50;
    

    .discover-overlay-title {
        text-align: center;
        padding: 20px;
        border-bottom: 1px solid white;
        font-weight: 600;
        font-size: 20px;
    }

    .discover-overlay-desc {
        padding: 20px;
    }

    svg {
        position: absolute;
        right: 10px;
        top: 20px;
        cursor: pointer;
        background-color: white;
        border-radius: 50%;
        height: 20px;
        width: 20px;
        padding: 5px;
    }

    svg:hover {
        background-color: rgb(223, 43, 43);
    }

}
.left {
    position: fixed;
    top: 50%;
    left: 200px;
}
.discover-btn-cards{
    display: block;
    background-color: white;
    color: black;
    border:1px solid black;
    border-radius: 5px;
    padding:10px 15px;
    margin:20px auto;
    width:90%;
}

//mobile view: -
@media (min-width:300px) and (max-width:600px){
.discover-cards{
    max-width:90%;
}
.discover-overlay {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgb(255, 255, 255);
    color: rgb(0, 0, 0);
    border: 1px solid black;    
    max-width: 90%;
    height: auto;   
    z-index: 50;
    .discover-overlay-title{
        border-bottom: 1px solid black;
    }
}
}

