.overview-interiorhero {
    background-color: rgb(241, 239, 237);
    padding: 20px auto;
    display: flex;
    flex-direction: column;
    width: 100%;
    height: auto;

    .overview-interiorhero-details {
        width: 1050px;
        margin: 40px auto;

        p {
            color: rgb(80, 82, 84);
            font-size: 16px;
            padding: 15px 0px;
        }

        h1 {
            font-weight: 600;
        }

        img {
            max-width: 95%;
            height: auto;
            margin: 10px auto;
        }
    } 
}

//ipads
@media (min-width:601px) and (max-width:1200px) {
    .overview-interiorhero-details {
        max-width: 80%;
        margin: 40px auto;

        img {
            max-width: 80%;
            height: auto;
            margin: 10px auto;
        }
    }
}

@media (min-width:1px) and (max-width:600px) {
    .overview-interiorhero-details {
        max-width: 80%;
        margin: 20px auto;

        img {
            max-width: 80%;
            height: auto;
            margin: 5px auto;
        }
    }
}