.info-container{
    display: flex;
    justify-content:space-between;
    background-color: black;
    align-items: center;
    color:white;
    font-size: 12px;
    height: 50px;
    .info-body{
        display: flex;
        margin:0px auto;
        justify-content: center;
        align-items: center;
        p{
            padding: 0px 20px;
        }
        .info-link{
            color: white;
            text-decoration: none;
            text-transform:uppercase;
            font-weight: bold;
            font-size:small;
            padding-right:5px ;  
        }
        .info-link:hover{
            border-bottom: 1px solid white;
        }
        .info-arrow{
            padding:auto 0px;
            font-size: 14px;
        }
    }
    .info-cancel svg{
        background-color: rgb(255, 255, 255);
        color:white;
        margin:auto 20px;
        border-radius: 50%;
        height:10px;
        width:10px;
        padding:5px;
        border:1px solid black;
        cursor: pointer;
    }
    .info-cancel svg:hover{
        background-color: red;
    }
    
}
//mobile views: -
@media  (max-width: 600px) 
{
    .info-container{
        width:100%;
        height:80px;

    }
    .info-body{
        flex-direction: column;
        justify-content: space-between;
        width:100%;
        height: auto;
    }
}
