.digital-services-container {
    max-width: 1200px;
    width: 100%;
    display: flex;
    margin: 40px auto;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row-reverse;
    flex-wrap: wrap;
    height: auto;

    .digital-services-asset {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        width: 500px;
        height: 500px;

        img {
            width: 400px;
            height: 300px;
        }

        .digital-services-headline {
            font-size: 30px;
            font-weight: 600;
            margin: 20px auto;
        }
    }

    .digital-services-body {
        width: 500px;
        display: flex;
        flex-direction: column;
        background-color: rgb(249, 248, 248);
        justify-content: space-evenly;
        align-items: center;
        padding: 40px;
        height: 500px;

        .digital-services-items {
            width: 400px;

            .digital-services-item-title {
                font-size: 15px;
                font-weight: 600;
                margin: 5px auto;
            }

            .digital-services-item-desc {
                color: rgb(89, 91, 91);
                margin-bottom: 40px;
            }
        }
    }
}
.digital-services-disclaimer {
    text-align: center;
    width: auto;
    max-width: 80%;
    margin: 50px auto;
    color:rgb(105, 105, 105);
}

@media (min-width:300px) and (max-width:600px) {
    .digital-services-container {
            width: 300px;
            height:auto;
            .digital-services-asset{
                img{
                    width:250px;
                    height:300px;
                }
                .digital-services-headline {
                    font-size: 20px;
                    font-weight: 600;
                    margin: 0px auto;
                }
            }
            .digital-services-body{
                width:80%;
                height: auto;
                .digital-services-items {
                    width: 90%;
                
                }
                
                
            }

        }

}