.harman-kardon-title{
    text-align: center;
    font-size: 30px;
    margin:40px auto;
    font-weight: 600;
}
.harman-kardan-container{
    width:1000px;
    display: flex;
    margin:40px auto;
    flex-wrap: wrap;
    align-items: center;
    .harman-kardan-image{
        width:60%;
        
        img{
            width:480px;
            height:600px;
        }
    }
    .harman-kardan-content{
        width:40%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .harman-kardan-item-title{
            font-size: 30px;
            font-weight: 600;
            font-size: 26px;
            margin:10px auto;
        }
        .harman-kardan-item-desc{
            color:rgb(94, 95, 95);
            margin-bottom: 10px;
        }
    }
}
//tablets
@media (min-width:601px) and (max-width:1200px){
    .harman-kardan-container{
        max-width:800px;
        flex-direction: column;
        align-items: center;
        justify-content: space-evenly;
        .harman-kardan-image,.harman-kardan-content{
            width:60%;
            text-align: center;
        }
    }
}
//mobile views: -
@media (min-width:300px) and (max-width:600px){
    .harman-kardon-title{
        font-size: 20px;
    }
    .harman-kardan-container{
        max-width:280px;
        align-items: center;
        .harman-kardan-image,.harman-kardan-content{
            width:100%;
            text-align: center;
            img{
                width:280px;
                height:400px;
            }
        }

    }
}