.model-cards{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    column-gap: 2px;
    row-gap: 2px;
    width:100%;
    text-align: center; 
    margin:100px auto; 
    width:1250px; 
}

 @media (min-width:1px) and (max-width:1200px)
{
    .model-cards{
        grid-template-columns: repeat(2,1fr);
        width:90%;
        column-gap: 10px;
        row-gap: 10px;
        margin: 50px auto; 
    }

}