.overview-callouts-container{
    margin:80px auto;
    width:1100px;
    display: flex;
    justify-content: space-evenly;
    align-items: start;
    height: auto;
    flex-wrap: wrap;
}

@media (min-width:1px) and (max-width:1200px){
    .overview-callouts-container{
        flex-direction: column;
        width:fit-content;
    }
}