.overview-spec-container {
    display: flex;
    margin: 70px auto 40px auto;
    justify-content: space-evenly;
    align-items: center;
    width: 1100px;

    img {
        width: 500px;
        height: 600px;
    }

    .overview-spec-data {
        margin: auto 60px;
        width: 40%;

        .overview-spec-title {
            margin: 20px auto;
            font-size: 35px;
            font-weight: 600;
        }

        .overview-spec-values {
            font-size: 18px;
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            row-gap: 20px;
            column-gap: 20px;
            width: auto;
            height: auto;

            p {
                font-weight: 600;
                padding: 5px auto;

                .value {
                    font-weight: 400;
                    color: rgb(91, 90, 90);
                }
            }
        }
    }
}
.overview-spec-disclaimer {
    width: 1100px;
    margin: 20px auto;
    font-size: 14px;
    color: rgb(111, 111, 111);
    text-align: center;
}

@media (min-width:250px) and (max-width:1200px) {
    .overview-spec-container {
        flex-direction: column;
        width: auto;
        margin: 50px auto;

        img {
            width: 80%;
            height: 100%;
        }

        .overview-spec-data {
            margin: 20px auto;
            width: 80%;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
    .overview-spec-disclaimer {
        width: 80%;
        margin: 20px auto;
        font-size: 14px;
        color: rgb(111, 111, 111);
    }

}