.overview-intro-container{
   display: flex;
   justify-content: space-between;
   align-items: center;
   height:200px;
   max-width:1100px;
   flex-wrap: wrap;
   margin: 20px auto;
   .overview-intro-left{
      width:60%;
      font-size: 45px;
      font-weight: 600;
      color:rgb(90, 87, 87);
      // line-height: 1.2;
      .intro-bold{
         color: black;
      }
   }
   .overview-intro-right{
      width:40%;
      display: flex;
      height: 160px;
      flex-direction: column;
      justify-content:space-evenly;
      align-items: center;
      .overview-intro-price{
         font-weight: 600;
         font-size: 20px;
      }
      .overview-intro-link .button{
         display: block;
      }
   }
}
//tablets: =
@media (min-width:601px) and (max-width:1200px){
   .overview-intro-container{
      height:auto;
      display: flex;
      flex-direction: column;
      
   }
   .overview-intro-left,.overview-intro-right{
      margin:20px auto;
   }
}
//mobile views: -
@media (max-width:600px){
   .overview-intro-container{
      height:auto;
      display: flex;
      flex-direction: column;
      
   }
   .overview-intro-left,.overview-intro-right{
      margin:10px auto;
      line-height: 1;
   }
   .overview-intro-left .overview-intro{
      font-size: 30px;
   }
   .overview-intro-price p{
      line-height: 1.2;
      font-size: 14px;
   }
}