.faq-container {
  max-width: 100%;
  margin: 70px auto;
  width: auto;
  height: auto;
  padding-bottom: 50px;
  background-color: #fafafa;

  .faq-headline {
    width:90%;
    text-align: center;
    font-size: 20px;
    font-weight: 600;
    padding: 40px 0px;
    margin:0px auto;
  }

  .faq-items {
    width: 60%;
    margin: 10px auto;
    padding: 10px 0px;
    border-bottom: 1px solid rgb(97, 95, 95);
    cursor: pointer;

    .faq-que {
      width: auto;
      position: relative;

      .faq-icons svg {
        position: absolute;
        left: 95%;
        top: 0%;
      }
    }

    .faq-ans {
      
      margin: 5px 0px;

    }
  }
}