.flatcards-container {
    max-width: 1000px;
    margin: 90px auto;

    .flatcards-title {
        font-size: 30px;
        font-weight: 600;
        margin: 40px auto;

    }

    .flatcards-cards-container {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: start;
        margin: 20px auto;


        .flatcard {
            width: 480px;
            

            .flatcard-title {
                margin: 15px 0px;
                font-size: 20px;
                font-weight: 600;
            }

            .flatcard-desc {
                color: rgb(77, 78, 78);
            }

            img {
                width: 480px;
                height:400px;
                border-radius:20px;
            }
        }
    }
}

//laptops: -
@media (min-width:601px) and (max-width:1200px) {
    .flatcards-container {
        width: 600px;
        height: auto;

        .flatcards-title {
            font-size: 30px;
            font-weight: 600;
            margin: 20px auto;
            text-align: center;

        }

        .flatcards-cards-container {
            align-items: center;
            justify-content: center;

            .flatcard {
                width: auto;
                margin: 20px auto;

                img {
                    width: 600px;
                    height: auto;

                }
            }
        }
    }
}

//mobiles: -
@media (min-width:301px) and (max-width:600px) {
    .flatcards-container {
        width: 300px;
        height: auto;

        .flatcards-title {
            font-size: 20px;
            font-weight: 600;
            margin: 20px 0px;
            text-align: center;
        }

        .flatcards-cards-container {
            align-items: center;
            justify-content: center;

            .flatcard {
                width: 240px;
                margin: 20px auto;

                img {
                    width: 240px;
                    height: auto;
                }
            }
        }
    }
}