.feature-model-intro-container{
    text-align: center;
    margin:60px auto;
    width:auto;
    max-width:600px;
    .feature-title{
        font-weight: 600;
        font-size: 40px;
        margin:10px auto;
    }
    .feature-desc{
        color: rgb(88, 90, 91);
        font-weight: 400;
        font-size: 20px;
        margin:20px auto;
    }
}

@media (min-width:300px) and (max-width:600px){
    .feature-model-intro-container{
        width:80%;
    }
}