.endlinks-container{
    width:1000px;
    margin:60px auto;
    .endlinks-title{
        font-size: 30px;
        font-weight: 600;
        margin:40px auto;
        padding-left: 60px;
        
    }
    .endlinks-models{
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        .endlinks-ex40,.endlinks-xc60{
            width:40%;

            .endlinks-ex40-subtitle,.endlinks-xc60-subtitle{
                font-weight: 600;
                margin:20px auto;
                font-size: 16px;
            }
            .endlinks-ex40-desc,.endlinks-xc60-desc{
                font-size: 16px;
                color: rgb(109, 111, 112);
                margin:20px auto;  
            }
            .endlinks-learn{
                margin-right:20px;
            }
            .endlinks-learn::after,.endlinks-shop::after{
                content: " >"
            }
            .endlinks-learn:hover,.endlinks-shop:hover{
                text-decoration: underline;
            }
        }

    }
    img{
        width:400px;
        height:300px;
    }
}
//tablets
@media (min-width:601px) and (max-width:1200px){
    .endlinks-container{
        width:90%;
        img{
            width:90%;
            height:90%;
        }
    }
}
//mobile views: -
@media (min-width:300px) and (max-width:600px){
    .endlinks-container{
        width:300px;
        img{
            width:200px;
            height:150px;
        }
        .endlinks-title{
            margin:20px auto;
            font-size:16px;
            text-align: center;
            padding: 0px;
        }
        .endlinks-models{
            flex-direction: column;
             .endlinks-ex40,.endlinks-xc60{
                width:80%;
                text-align: center;
                margin:20px auto;
        }

    }
}
}
