/* For WebKit browsers (Chrome, Safari) */

.menu-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding: 0px 20px;
  margin: 0px auto;
  max-width: 1250px;
  flex-wrap: wrap;
  position: sticky;
  top: 0px;
  background-color: white;
  z-index: 10;
  border-bottom: 1px solid rgb(181, 178, 178);
}

.nav-link {
  text-decoration: none;
  color: gray;
  margin: 0px 10px;
  font-size: 14px;
  font-weight: 600;
}

.nav-link:hover {
  color: rgb(19, 18, 18);
}

.nav-link.active {
  color: rgb(10, 10, 11);
  border-bottom: 2px solid rgb(81, 81, 218);
}

.menu-modelname {
  font-weight: bold;
  padding-right: 8px;
}

.menu-left span:nth-child(2) {
  padding-left: 15px;
}

.menu-right span {
  padding-left: 20px;
}

a {
  text-decoration: none;
}

.price {
  font-weight: 600;
  font-size: 14px;
}

@media (min-width: 601px) and (max-width:1200px) {
  .menu-container {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    height: 120px;
    flex-wrap: wrap;

  }

}

@media (max-width: 600px) {
  .menu-container {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    width:100%;
  }

  .menu-right {
    display: none;
  }
}